/**
 * <directive>
 * @description 
 * @date 2017-04-20
 * @author 
 * @lastBy 
 * @html <qrcode-directive url-address="urlAddress" ></qrcode-directive>
 */
define([
    'angular'
], function(
    angular) {
    'use strict';
    return function(app, elem, attrs, scope) {
        app.directive('qrcodeDirective', ['$http','$timeout', function($http, $timeout) {
            return {
                template: html,
                restrict: 'EA',
                replace: false,
                scope: {
                    list: '=',          //@scope list  {type: "array", "exampleValue": [{text:''},{text:''}]}
                    itemSelected: '=',  //@scope itemSelected  {type: "string","exampleValue":""}
                    isOptional: '=',    //@scope isOptional  {type: "boolean", "exampleValue": true}
                    isErrorInline: '=', //@scope isErrorInline  {type: "boolean", "exampleValue": true}
                    searchFromApi: '=', //@scope searchFromApi  {type: "function"}
                    displayText: '@'    //@scope displayText  {type: "string", "exampleValue":"text"}
                },
                link: function($scope, $element, $attrs) {

                },
               
                controller: function($scope,$element,$attrs,$transclude,$log,$http,G){
                    
                }
            }
        }])
    }
})



