import './index.less';
import html from './index.html';

export default (app, elem, attrs, scope) => {
  app.directive('fileUploader', [function(){
    return {
      template: html,
      restrict: 'EA',
      scope: {
        accept: '@', // ,  https://developer.mozilla.org/en-US/docs/Web/HTML/Element/input#attr-accept
        action: '@', // , 
        onChange: '=', // 
        beforeUpload: '=', //  false  Promise Promise  reject resolve 
        triggerId: '@', // 
        data: '=', // 
        name: '@', //   file,
      },
      transclude: true,
      link(scope, element) {
        const input = document.getElementById('upload-trigger');
        element.on('click', scope.triggerId, e => {
          input.value = null;
          input.click();
        });
      },
      controller($scope) {
        console.log('fileUploader', $scope);
        $scope.errorMsg = null;

        this.reset = () => {
          console.log('reset');
        };

        $scope.$watch('')

        if (typeof FormData !== 'function') {
          $scope.errorMsg = 'H5';
          return;
        }

        if (!$scope.triggerId) {
          $scope.errorMsg = ' triggerId';
          return;
        }
        if (!$scope.action) {
          $scope.errorMsg = '';
          return;
        }

        const input = document.getElementById('upload-trigger');
        const handleUpload = () => {
          const files = input.files;
          if (files.length === 0) return;
          const file = files[0];
          const hook = $scope.beforeUpload(file);
          if (!hook) return false;
          if (hook instanceof Promise) {
            hook.then(upload);
          } else {
            upload(hook);
          }
        };
        const upload = file => {
          const formData = new FormData();
          formData.append($scope.name || 'file', file);
          if ($scope.data) {
            Object.keys($scope.data).forEach(key => {
              formData.append(key, $scope.data[key]);
            });
          }
          fetch($scope.action, {
            method: 'post',
            data: formData,
            credentials: 'include',
          })
            .then(res => res.json())
            .then($scope.onChange)
            .catch(err => {
              $scope.$apply(() => {
                if (typeof err === 'object') {
                  $scope.errorMsg = err.toString();
                } else {
                  $scope.errorMsg = err;
                }
              });
            });
        };

        $(input).on('change', handleUpload);
        
      }
    }
  }]);
};


